import EasyStarHeader from '@/components/EasyStarHeader'
import EasyStarLeft from '@/components/EasyStarLeft'
import EasyStarMain from '@/components/EasyStarMain'

export default (Vue) => {
  Vue.component('EasyStarHeader', EasyStarHeader)
  Vue.component('EasyStarLeft', EasyStarLeft)
  Vue.component('EasyStarMain', EasyStarMain)
}



