export default [
  {
    path: '/',
    name: 'docMain',
    component: (resolve) => require(['@/components/ArchiveHome.vue'], resolve)
  },
  {
    path: '/docView',
    name: 'docView',
    component: (resolve) => require(['@/components/docManager/DocMain.vue'], resolve),
    props: true
  },
  {
    path: '/docStructSetup',
    name: 'docStructSetup',
    component: (resolve) => require(['@/pages/system/struct/StructSetup.vue'], resolve),
    props: true
  },
  {
    path: '/docReportSetup',
    name: 'docReportSetup',
    // redirect: {name: 'docView', props: true}
    component: (resolve) => require(['@/components/docManager/DocMain.vue'], resolve),
    props: true
  },
  {
    path: '/boxView',
    name: 'boxView',
    component: (resolve) => require(['@/components/docManager/DocMain.vue'], resolve),
    props: true
  },
  {
    path: '/boxStructSetup',
    name: 'boxStructSetup',
    component: (resolve) => require(['@/pages/system/struct/StructSetup.vue'], resolve),
    props: true
  },
  {
    path: '/boxReportSetup',
    name: 'boxReportSetup',
    component: (resolve) => require(['@/components/docManager/DocMain.vue'], resolve),
    props: true
  },
  {
    path: '/roolView',
    name: 'roolView',
    component: (resolve) => require(['@/components/docManager/DocMain.vue'], resolve),
    props: true
  },
  {
    path: '/roolStructSetup',
    name: 'roolStructSetup',
    component: (resolve) => require(['@/pages/system/struct/StructSetup.vue'], resolve),
    props: true
  },
  {
    path: '/roolReportSetup',
    name: 'roolReportSetup',
    component: (resolve) => require(['@/components/docManager/DocMain.vue'], resolve),
    props: true
  },
  {
    path: '/fileManage',
    name: 'fileManage',
    component: (resolve) => require(['@/components/docManager/DocMain.vue'], resolve),
    props: true
  },
  {
    path: '/logManage',
    name: 'logManage',
    component: (resolve) => require(['@/components/docManager/DocMain.vue'], resolve),
    props: true
  }
]



