<template>
  <div>
    <el-menu
      background-color="#3691EC"
      text-color="#fff"
      active-text-color="#ffd04b"
      :default-openeds="openeds"
      >
      <easy-star-menu :nav-menus="menuData" @itemClick="itemClick"></easy-star-menu>
    </el-menu>
  </div>
</template>
<script>
import easymenu from '@/components/docManager/nav/EasyStarMenu'
export default {
  name: 'EasyStarLeft',
  components: {
    EasyStarMenu: easymenu
  },
  created: function () {
    this.getMenuData()
  },
  data () {
    return {
      menuData: [],
      classId: '1EF80FCCD22E4AC9A44A14348EABD53A',
      classType: '2',
      openeds: ['docManage', 'boxManage', 'roolManage']
    }
  },
  methods: {
    itemClick: function (res) {
      var tmpType = '0'
      switch (res.name.substring(0, 3)) {
        case 'doc':
          tmpType = '0'
          break
        case 'box':
          tmpType = '1'
          break
        case 'roo':
          tmpType = '2'
          break
      }
      this.$router.push({
        name: res.name,
        params: {
          title: res.alias,
          classId: this.classId,
          classType: tmpType
        }
      })
    },
    getMenuData: function () {
      let that = this
      this.axios.get(this.api + '/ClassPanel.do', {
        params: {
          classId: this.classId,
          classType: this.classType
        }
      }).then(
        function (res) {
          that.menuData = res.data.list
        }
      )
    }
  }
}
</script>
<style>
.el-menu {
  border: 0px;
}
</style>



