<template>
  <div class="EasyStarMenu">
    <template v-for="navMenu in navMenus">
        <!-- 最后一级菜单 -->
      <el-menu-item v-if="!navMenu.childs&&navMenu.entity"
                    :key="navMenu.entity.id"
                    :data="navMenu"
                    :index="navMenu.entity.name"
                    @click="clickMenuItem(navMenu.entity)"
                  >
        <i :class="navMenu.entity.icon"></i>
        <span slot="title">{{navMenu.entity.alias}}</span>
      </el-menu-item>
      <!-- 此菜单下还有子菜单 -->
      <el-submenu v-if="navMenu.childs&&navMenu.entity"
                  :key="navMenu.entity.id"
                  :data="navMenu"
                  :index="navMenu.entity.name"
                >
        <template slot="title">
          <i :class="navMenu.entity.icon"></i>
          <span>{{navMenu.entity.alias}}</span>
        </template>
        <!-- 递归 -->
        <EasyStarMenu :navMenus="navMenu.childs" @itemClick="itemClick"></EasyStarMenu>
      </el-submenu>
    </template>
  </div>
</template>

<script>
export default {
  name: 'EasyStarMenu',
  data () {
    return {
    }
  },
  props: ['navMenus'],
  methods: {
    clickMenuItem: function (res) {
      this.$emit('itemClick', res)
    },
    itemClick: function (res) {
      this.$emit('itemClick', res)
    }
  }
}
</script>

<style>
</style>



